body, html, #root {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: Inter, sans-serif;
    -webkit-font-smoothing: antialiased; /* Apply font smoothing */
    -moz-osx-font-smoothing: grayscale; /* Apply font smoothing */
    overflow: hidden; /* Prevent scrollbars at the body level */
  }
  
  @font-face {
    font-family: 'GT-Alpina';
    src: url('./fonts/GT-Alpina-Fine-Condensed-Thin.woff2') format('woff2');
  }
  
  h1 {
    font-family: 'GT-Alpina';
    font-size: 3em;
    color: #333;
  }
  
  pre {
    font-family: Inter;
  }
  
  .app-container {
    width: 100%;
    height: 100%;
    position: relative;
  }
  
  .page {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  
  .detail-page, .home-page {
    height: 100%;
    overflow: hidden; /* Ensure no overflow for the specific page content */
  }
  
  .background {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    transform: translateY(-70%); /* Center vertically */
    display: flex;
    justify-content: center; /* Center horizontally */
    z-index: -1; /* Ensure the background stays behind other content */
  }
  
  .background-svg {
    max-width: 100%;
    max-height: 100%;
    /* Additional styles for the SVG if needed */
  }
  
  .background h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 48px;
  }
  
  .floating-panel-container {
    position: fixed; /* Fix the container's position */
    bottom: 5%; /* Align it to the bottom */
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    pointer-events: none; /* Prevent this container from affecting other elements */
  }
  
  .floating-panel-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    pointer-events: none;
  }
  
  .floating-panel-wrapper > * {
    pointer-events: auto;
  }
  
  .floating-panel {
    will-change: transform, filter;
    background: #F9F9F9;
    background-blend-mode: luminosity;
  
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    padding: 10px 20px;
    border-radius: 28px;
    border: 1.4px solid #d8d8d8cd;
    z-index: 1000;
    white-space: nowrap;
    pointer-events: auto; /* Allow interaction */
  }
  
  .app-icon {
    position: relative;
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .app-icon img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 18px;
  }
  
  .app-icon:hover .tooltip {
    visibility: visible;
    opacity: 1;
  }
  
  .tooltip {
    position: absolute;
    bottom: 130%;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
    border: 0.794px solid #D8D8D8;
    background: #F9F9F9;
    color: #333;
    padding: 5px 14px;
    visibility: hidden;
    opacity: 0;
    z-index: 1001;
    transition: opacity 0.3s;
  }
  
  .divider {
    width: 1px;
    height: 74px; /* Adjust height as needed */
    background-color: #565656; /* Divider line color */
    margin: 0 5px; /* Adjust spacing between icons */
  }
  
  .home-button {
    border-radius: 28px;
    border: 1.4px solid #d8d8d8cd;
    background: #F9F9F9;
    background-blend-mode: luminosity;
    padding: 8px 34px;
    z-index: 1000; /* Ensure it's above the floating panel */
    pointer-events: auto; /* Allow interaction */
    margin-bottom: 10px; /* Space between home button and floating panel */
  }
  
  .detail-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh; /* Ensure full viewport height */
    overflow: hidden; /* Prevent scrolling on the main container */
  }
  
  .picture-section {
    position: relative;
    width: 100%;
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  .picture {
    width: 98%;
    height: 94%;
    max-width: 100%;
    object-fit: cover;
    border-radius: 22px;
  }
  
  .prev-button,
  .next-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .prev-button {
    left: 30px;
  }
  
  .next-button {
    right: 30px;
  }
  
  .prev-button:hover,
  .next-button:hover {
    opacity: 0.6;
  }
  
  .prev-button svg,
  .next-button svg {
    width: 36px;
    height: 36px;
   /* Adjust fill color as needed */
  }
  
  .content-wrapper {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 60%;
    height: 30vh;
    overflow-y: auto;
    display: flex;
  }
  
  .text-section {
    width: 80%;
    flex-direction: column;
    box-sizing: border-box;
    padding-right: 20%;
  }
  
  .text-section p {
    max-width: 100%;
    color: #666;
  }
  
  .additional-content {
    padding-top: 40px;
  }